@charset "UTF-8";
@keyframes empty-collection-arrow {
  0%,
  25%,
  100% {
    transform: translateY(0);
  }
  5%,
  15% {
    transform: translateY(-4px);
  }
  10%,
  20% {
    transform: translateY(4px);
  }
}

.neos-contentcollection > [data-__neos-fusion-path*="removedContent"]:first-child,
.neos-contentcollection > [class^="style__addEmptyContentCollectionOverlay"], body > main[data-__neos-node-contextpath]:empty {
  cursor: pointer;
  height: 41px;
  text-align: right;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.neos-contentcollection > :first-child[data-__neos-fusion-path*="removedContent"]::before,
.neos-contentcollection > [class^="style__addEmptyContentCollectionOverlay"]::before, body > main:empty[data-__neos-node-contextpath]::before, .neos-contentcollection > :first-child[data-__neos-fusion-path*="removedContent"]::after,
.neos-contentcollection > [class^="style__addEmptyContentCollectionOverlay"]::after, body > main:empty[data-__neos-node-contextpath]::after {
  display: inline-block;
  color: #adadad;
  opacity: 0.5;
  transition: opacity 0.2s ease;
  line-height: 41px;
}

.neos-contentcollection > :first-child[data-__neos-fusion-path*="removedContent"]::after,
.neos-contentcollection > [class^="style__addEmptyContentCollectionOverlay"]::after, body > main:empty[data-__neos-node-contextpath]::after {
  margin: 0 1rem;
  content: "\21e7";
  animation: empty-collection-arrow 2.5s infinite 2s;
}

.neos-contentcollection > :first-child[data-__neos-fusion-path*="removedContent"]::before,
.neos-contentcollection > [class^="style__addEmptyContentCollectionOverlay"]::before, body > main:empty[data-__neos-node-contextpath]::before {
  content: "Click here to add content";
}

.neos-contentcollection > :first-child[data-__neos-fusion-path*="removedContent"]:hover::before,
.neos-contentcollection > [class^="style__addEmptyContentCollectionOverlay"]:hover::before, body > main:empty[data-__neos-node-contextpath]:hover::before, .neos-contentcollection > :first-child[data-__neos-fusion-path*="removedContent"]:hover::after,
.neos-contentcollection > [class^="style__addEmptyContentCollectionOverlay"]:hover::after, body > main:empty[data-__neos-node-contextpath]:hover::after {
  opacity: 1;
}

.neos-contentcollection > [data-__neos-fusion-path*="removedContent"]:first-child {
  box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.3);
}

.neos-contentcollection > [data-__neos-fusion-path*="removedContent"]:first-child,
.neos-contentcollection > [class^="style__addEmptyContentCollectionOverlay"], body > main[data-__neos-node-contextpath]:empty {
  margin: 2rem auto;
}

.neos-contentcollection > :first-child[data-__neos-fusion-path*="removedContent"]::before,
.neos-contentcollection > [class^="style__addEmptyContentCollectionOverlay"]::before, body > main:empty[data-__neos-node-contextpath]::before {
  content: "Hier klicken um Inhalt hinzuzufügen";
}

body > main[data-__neos-node-contextpath]:empty {
  margin: 250px auto;
  box-shadow: 0 0 0 0.5em #fafafa, 0 0 0 calc(0.5em + 2px) rgba(0, 0, 0, 0.2);
  min-height: 0;
  max-width: calc(100vw - 60px);
  padding: 0;
}
